
import { defineComponent, onMounted, ref } from "vue";
export default defineComponent({
  props: {
    label: {
      type: String,
    },
    type: {
      type: String,
      default: "default",
    },
    size: {
      type: String,
      default: "",
    },
    autoFocus: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const buttonReference = ref<HTMLButtonElement | null>(null);

    onMounted(() => {
      const button = buttonReference.value;

      if (props.autoFocus && button) {
        button.focus();
      }
    });

    return {
      buttonReference,
    };
  },
});
